:root {
  --primary-blue: hsl(219, 29%, 15%);
  --secondary-blue: hsl(229, 89%, 85%);
  --accent-orange: hsl(31, 100%, 67%);
  --accent-green: #a5cc75;
  --accent-purple: hsl(260, 54%, 67%);
  --text-color: hsl(217, 100%, 97%);
  --inactive-text-color: hsla(216, 86%, 67%, .40);
}

:root {
  --font-inter: "Inter", sans-serif;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: auto;
  padding: 0;
  width: 75%;
  font-family: var(--font-inter);
  background-color: var(--primary-blue);
  color: var(--text-color);
}

h1,
h2,
h3 {
  margin: 1.25rem;
  margin-top: 0;
}

h1 {
  font-size: 2.25rem;
  font-weight: 700;
}
@media (min-width: 64em) {
  h1 {
    font-size: 3rem;
  }
}

h2 {
  font-size: 1.25rem;
  font-weight: 500;
  margin-top: 0.3125rem;
}

h3 {
  font-size: 0.875rem;
  font-weight: 600;
  text-transform: uppercase;
}

p {
  margin: 0 1.25rem;
  font-size: 0.875rem;
  font-weight: 500;
}

a,
a:visited,
a:active {
  font-size: 0.75rem;
  text-transform: uppercase;
  margin-left: 1.25rem;
  text-decoration: none;
}

span {
  font-size: 0.875rem;
  font-weight: 400;
  text-transform: uppercase;
}

ul {
  list-style-type: none;
  padding: 0;
  margin: 1.25rem;
}

main {
  display: flex;
}
.section {
  flex-basis: 100%;
}
.section__pane--left {
  position: sticky;
  top: 20px;
  flex-basis: 65vw;
}
.subsection {
  margin: 12.5rem 0;
}

.title__location {
  display: flex;
  align-items: center;
  margin: 15px;
}
.title__location img {
  padding-right: 5px;
}

.nav {
  margin-top: 3.125rem;
  height: 25rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.nav__links {
  display: flex;
  padding: 0;
}
.nav__links--section {
  flex-direction: column;
  gap: 10px;
}
.nav__links--social {
  flex-direction: row;
}

.link:hover {
  color: var(--text-color);
  filter: var(--accent-green);
}
.link--active {
  color: var(--text-color);
}
.link--inactive {
  color: var(--inactive-text-color);
}
.link__social {
  height: 24px;
}
.link__social--github, .link__social--linkedin {
  filter: invert(100%) sepia(3%) saturate(12%) hue-rotate(103deg) brightness(105%) contrast(105%);
}
.link__social--github:hover, .link__social--linkedin:hover {
  filter: invert(84%) sepia(28%) saturate(585%) hue-rotate(38deg) brightness(90%) contrast(85%);
}

.tech__category {
  padding: 0.625rem 0;
}
.tech__list {
  display: grid;
  grid-template-columns: repeat(5, min-content);
  gap: 2.8125rem;
  margin-left: 1.25rem;
}

.item {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.75rem;
}
.item__imagebg {
  height: 3rem;
  width: 3rem;
  background-color: var(--text-color);
  border-radius: 1.875rem;
}

.imagebg__image {
  filter: invert(67%) sepia(80%) saturate(300%) hue-rotate(196deg) brightness(102%) contrast(97%);
  transition: filter 200ms;
  height: 2.5rem;
  width: 2.5rem;
  margin: 0.25rem;
}
.imagebg__image:hover {
  filter: none;
}
.imagebg__image--link {
  height: 0.9375rem;
  width: 0.9375rem;
  margin: 0 0 1.25rem;
}

.work {
  display: flex;
  flex-direction: row;
  margin-top: 2.8125rem;
}
.work__date {
  color: var(--accent-purple);
  flex-basis: 30.3125rem;
}

.description {
  margin: 0;
}

.techlist {
  margin: 0.9375rem;
}
.techlist__tech {
  padding: 0.5rem;
  margin: 0.3125rem;
  border-radius: 1.875rem;
  background-color: var(--accent-purple);
}